}
.chatbox {
  width: 100%;
  height: 100%;
  justify-content: space-between;
}
chat-input {
  float: top;
}

.body-wrapper {
  background-color: #DCDCDC;
  height: calc(100% - 90px);
  overflow-y: scroll;
  width: 100%;
}

.self-wrap {
  /*background-color: #666;*/
  padding: 5px 5px 15px 5px;
  position: relative;
  overflow: hidden;
  /*
  .-header{
  	position: relative;
