}
.self-wrap .-header {
  position: relative;
  left: calc(100% - 50px);
  border-radius: 3px;
  height: 35px;
  width: 35px;
}
.self-wrap .-msg {
  background-color: #b2e281;
  padding: 10px;
  float: right;
  position: relative;
  left: -60px;
  top: 0px;
  border-radius: 6px;
  font-size: 13px;
  width: fit-content;
  width: -webkit-fit-content;
  width: -moz-fit-content;
  max-width: 200px;
  word-wrap: break-word;
}

.sys-wrap {
  /*background-color: #fff;*/
  padding: 3px;
  display: flex;
  justify-content: center;
  align-items: center;
}
.sys-wrap .sys-msg {
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: rgba(0, 0, 0, 0.05);
  border-radius: 10px;
  min-width: 40%;
  color: #999;
  font-size: 12px;
  padding: 3px;
}

.other-wrap {
  /*background-color: #666;*/
  padding: 5px 5px 0 5px;
  position: relative;
}
.other-wrap .-header {
  justify-content: center;
  border-radius: 3px;
  height: 35px;
  width: 35px;
}
.other-wrap .-name {
  /*background-color: #fff;*/
  position: absolute;
  font-size: 14px;
  padding: 2px;
  margin: 0 10px;
  color: #999;
}
.other-wrap .-msg {
  /*display: inline-flex;*/
  background-color: #fff;
  padding: 10px;
  position: relative;
  left: 40px;
  top: -40px;
  border-radius: 6px;
  font-size: 13px;
  width: fit-content;
  width: -webkit-fit-content;
  width: -moz-fit-content;
  max-width: 200px;
  word-wrap: break-word;
}

.foot-wrapper {
  background-color: #f1f1f1;
  height: 40px;
  flex-shrink: 0;
  display: flex;
  padding: 5px 10px;
  justify-content: space-between;
  border-top: solid 1px rgba(0, 0, 0, 0.1);
}
.foot-wrapper .chat-input-line {
  height: 100%;
  width: 70%;
  border-radius: 10px;
  outline: none;
  border: none;
  box-sizing: border-box;
  padding: 5px;
}
.foot-wrapper .chat-sub {
  display: flex;
  background-color: rgba(0, 0, 0, 0.1);
  justify-content: center;
  align-items: center;
  height: 100%;
  width: 12%;
  border-radius: 10px;
  outline: none;
}
.foot-wrapper .operator-sub {
  display: flex;
  background-color: rgba(0, 0, 0, 0.1);
  justify-content: center;
  align-items: center;
  height: 100%;
  width: 14%;
  margin-right: 10px;
  border-radius: 10px;
  outline: none;
}
.foot-wrapper .operator-sub,
.foot-wrapper .chat-sub:hover {
  cursor: pointer;
}
.foot-wrapper .primary {
  background-color: #1E90FF;
  color: white;
}

